import { useState } from "react";
import "./styles.css";
const data = require("./data.json");

export default function App() {
  const [value, setValue] = useState("");
  const onChange = (e) => {
    setValue(e.target.value);
  };
  const onSearch = (e) => {
    console.log("your search is", e);
  };
  return (
    <div className="App">
      <h1>Search</h1>
      <div className="search-container">
        <div className="search-inner">
          <input
            className="search-input"
            type="text"
            value={value}
            onChange={onChange}
          />
          <button type="button" onClick={() => onSearch(value)}>
            Search
          </button>
        </div>
        <div className="dropdown">
          {data
            .filter((e) => {
              const search = value;
              const color = e.color;
              return search && color.startsWith(search) && color !== search;
            })
            .map((e) => (
              <div
                onClick={() => onSearch(e.color)}
                key={e.color}
                className="dropdown-row"
              >
                {e.color}
              </div>
            ))}
        </div>
      </div>
    </div>
  );
}
